<template>
	<div
		class="basic-round"
		:class="{ active: active === item.id }"
		@click="handleRoundClick(item)"
		v-for="item in list"
		:key="item.id"
	>
		{{ item.name }}
	</div>
</template>

<script setup lang="ts">
	import { ref, watch } from 'vue'
	import { TabsItem } from '../BasicTabs/type'

	interface Props {
		list: TabsItem[]
		color?: string
		defaultValue?: string
	}

	const props = withDefaults(defineProps<Props>(), {
		list: () => [],
		color: '#a0cfff',
		defaultValue: ''
	})
	const emit = defineEmits(['change'])

	const active = ref<string>(props.list[0]?.name)

	watch(
		() => props.defaultValue,
		val => {
			if (val) {
				active.value = val
				emit('change', val)
			}
		}
	)

	function handleRoundClick(item) {
		active.value = item.id
		emit('change', item.id)
	}
</script>

<style scoped>
	.basic-round {
		display: inline-block;
		margin: 0 5px;
		padding: 3px 20px;
		border-radius: 50px;
		background-color: var(--el-color-info-light-8);
		color: var(--el-color-info);
		transition: 0.2s ease-out;
		font-size: 14px;
		cursor: pointer;
		&:hover {
			color: #282a2d;
			background-color: v-bind(props.color);
		}
		&.active {
			color: #282a2d;
			background-color: v-bind(props.color);
		}
	}
</style>
